(function(){	const viewWidth=document.documentElement.clientWidth/7.5,		   bodyWidth=document.documentElement.clientWidth;	const $fontSize=viewWidth>=64?64:viewWidth,		   $width=bodyWidth>=414?414:bodyWidth;	const $el=document.documentElement || window.body;	const $params={		margin:'0 auto',		fontSize:$fontSize+'px',		width:$width+'px'	};	setStyle($el,$params)})();function setStyle(el,params){	for(let i in params){		el.style[i]=params[i];	}}


